import React, { useEffect } from "react";
import { isIOS, isAndroid, isBrowser, isTablet } from "react-device-detect";

import { motion } from "framer-motion";

/**
 *  Navigation Component Type
 */
declare global {
  interface Window {
    Kakao: any;
    Tmapv2: any;
  }
}

/**
  @title Navigation Components
*/
export const Navigation = () => {
  const position = {
    lng: 126.9186859271572,
    lat: 37.55715071108653,
  };

  /** *   */
  const openKakaoNavi = async () => {
    if (window.Kakao) {
      if (isIOS || isAndroid || isTablet) {
        // SDK 
        window.Kakao.init("%REACT_APP_KAKAO_JAVASCRIPT_KEY_WEB%");

        // SDK  
        await window.Kakao.Navi.start({
          name: "", //
          x: position.lng, // 
          y: position.lat, // 
          coordType: "wgs84",
        });
      } else if (isBrowser) {
        console.error("Kakao SDK is not loaded.");
        return false;
      }
    }
  };

  /** *   */
  const openTmapNavi = async () => {
    if (isIOS) {
      //  ios  
      window.location.replace(
        `tmap://route?rGoName=""&rGoX=${position.lng}&rGoY=${position.lat}`
      );
    } else if (isAndroid) {
      //  android  
      window.location.replace(
        `tmap://route?referrer=com.skt.Tmap&goalx=${position.lng}&goaly=${position.lat}&goalname=""`
      );
    } else if (isTablet || isBrowser) {
      console.error("Tmap SDK is not loaded.");
      return false;
    }
  };

  return (
    <div>
      <motion.button
        onClick={openKakaoNavi}
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.9 }}
        style={{ cursor: "pointer", textDecoration: "none" }}
      >
        
      </motion.button>

      <motion.button
        onClick={openTmapNavi}
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.9 }}
        style={{ cursor: "pointer", textDecoration: "none" }}
      >
        
      </motion.button>
    </div>
  );
};
